import React, { Component } from 'react';
import * as firebase from 'firebase';
import Login from './components/Login';
import Profile from './components/Profile';
import ProfileEdit from './components/profileEdit';
import Slider from './components/Slider';
import './App.css';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Route, Link, Redirect } from 'react-router-dom';
const styles = {
	root: {
		flexGrow: 1
	},
	grow: {
    flexGrow: 2,
    paddingLeft:10,
	},
	menuButton: {
		marginLeft: -12,
		marginRight: 20
	}
};


class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			user: null,
			drawerOpen: false
		};
	
	}
	componentDidMount() {
		firebase.auth().onAuthStateChanged((user) => {
			if (user) {
				this.setState({ user });
			} else {
				this.setState({ user: null });
			}
		});
	}

	logout() {
		if (this.state.user) {
			firebase.auth().signOut().then((_) => {
				window.location.href = '/';
			});
		}
	}

	toggleDrawer(open) {
		this.setState({
			drawerOpen: open
		});
	}

	render() {
		const classes = styles;
		var logButtonName = this.state.user ? 'Logout' : 'Login';

		return (
			<div className={classes.root}>
				<AppBar position="static">
					<Toolbar>
        
				<IconButton style={classes.menuButton} color="inherit" aria-label="Menu"
                className='mobile-view'
                onClick={_=>this.toggleDrawer(true)}>
                <MenuIcon />
              </IconButton> 
              
						<Typography variant="title" color="inherit" style={classes.grow}>
							NEXT TRIP
						</Typography>
						<Drawer
            className='mobile-view'
							open={this.state.drawerOpen}
							onClose={(_) => this.toggleDrawer(false)}
						>
							<div
								tabIndex={0}
                
								role="button"
								onClick={(_) => this.toggleDrawer(false)}
								onKeyDown={(_) => this.toggleDrawer(false)}
							>
             
								<List  component="nav">
									<ListItem button component={Link} to="Packages">
										<ListItemText primary="Packages" />
									</ListItem>
					
									<ListItem button component={Link} to="profile">
										<ListItemText primary="Profile" />
									</ListItem>
									<ListItem button component={Link} to="login">
										<ListItemText primary={logButtonName} />
									</ListItem>
								</List>
               
							</div>
						</Drawer>
						<Button className="desktop-view" component={Link} to="/" color="inherit">
							home
						</Button>
						<Button className="desktop-view" component={Link} to="/Packages" color="inherit">
            Packages
						</Button>
						{this.state.user ? (
							<Button className="desktop-view" component={Link} to="/profile" color="inherit">
								profile
							</Button>
						) : null}
					  <Button className='desktop-view' component={Link} to="/login" color="inherit" onClick={_=>this.logout()}>{logButtonName}</Button>
           
					</Toolbar>

				</AppBar>
				<Route exact path="/" render={() =><Slider/>} />
        <Route exact exact path="/login" render={()=>(this.state.user? (<Redirect to ='/profile'/>):(<Login/>))}/>
		<Route exact path="/profile" render={() =><Profile/>} />
		<Route exact path="/profile-edit" render={() =><ProfileEdit/>} />

		
			</div>
		);
	}
}
export default App;
