import React, { Component } from 'react';
import abu from "./images/abu.jpg"
import abra from "./images/abra.jpg"
import "./slide.css"
import acadia from "./images/acadia.jpg"
import advent from "./images/advent.jpg"
import alex from "./images/alex.jpg"
import corin from "./images/corin.jpg"
import cremona from "./images/cremona.jpg"
import duisburg from "./images/duisburg.jpg"

import { Carousel } from "react-responsive-carousel";

  

class Slider extends Component {

    render() {
		return (
           
        <Carousel showArrows={true} autoPlay={4000} infiniteLoop={true} showStatus={false}showThumbs={false} >
                <div>
                    <img src={abu} />
                    <p className="legend">Legend 1</p>
                </div>
                <div>
                    <img src={abra} />
                    <p className="legend">Legend 2</p>
                </div>
                <div>
                    <img src={acadia} />
                    <p className="legend">Legend 3</p>
                </div>
                <div>
                    <img src={advent} />
                    <p className="legend">Legend 4</p>
                </div>
                <div>
                    <img src={alex} />
                    <p className="legend">Legend 5</p>
                </div>
                <div>
                    <img src={corin} />
                    <p className="legend">Legend 6</p>
                </div>
                <div>
                    <img src={cremona} />
                    <p className="legend">Legend 7</p>
                </div>
                <div>
                    <img src={duisburg} />
                    <p className="legend">Legend 8</p>
                </div>
               
            </Carousel>
        );
        }


}
export default Slider;