import React from "react";
import { Link } from "react-router-dom";
import edit from "./images/edit.png";
import { Button, Card, CardContent, Typography } from "@material-ui/core/";
import firebase from "firebase";

var db = null;

const loginStyle = {
  position: "absolute",
  paddingTop: 10,
  left: "50%",
  transform: "translate(-50%)",
  width: "100%",
  maxWidth: "1100px"
};
const styles = {
  card: {
    width: "100%"
  },
  textField: {
    marginRight: 20
  },

  input: {
    display: "none"
  }
};

export default class Profile extends React.Component {
  constructor(props) {
    super(props);
    const NOT_AVAILABLE = 'Not Available';
    this.state = {
      flag: false,
      id: NOT_AVAILABLE,
      user: NOT_AVAILABLE,
      firstName:NOT_AVAILABLE,
      middleName: NOT_AVAILABLE,
      lastName: NOT_AVAILABLE,
      mobileNumber: NOT_AVAILABLE,
      emergencyContactNumber: NOT_AVAILABLE,
      email:NOT_AVAILABLE,
      emailAlternate: NOT_AVAILABLE,
      blood: NOT_AVAILABLE,
      dob:NOT_AVAILABLE,
      address:NOT_AVAILABLE,
      state:NOT_AVAILABLE,
      city: NOT_AVAILABLE,
      country:NOT_AVAILABLE,
      pincode:NOT_AVAILABLE,
      profilePicture: NOT_AVAILABLE
    };

    db = firebase.firestore();
    db.settings({
      timestampsInSnapshots: true
    });
    
  }
  componentWillMount() {
    firebase.auth().onAuthStateChanged(user => {
      if(user) this.fetchProfileData(user);
    });
  }

  fetchProfileData(user) {
    this.setState({ user: user });
    db.collection("users")
    .where("id", "==", user.uid)
    .get()
    .then(snap => {
      snap.docs.forEach(docs => {
        const data = docs.data();
        this.setState({
          middleName: data.middleName,
          firstName: data.firstName,
          lastName: data.lastName,
          email: data.email,
          emailAlternate: data.emailAlternate,
          mobileNumber: data.mobileNumber,
          blood: data.blood,
          dob: data.DOB,
          emergencyContactNumber: data.emergencyContactNumber,
          address: data.Address
        });
      });
    });
  }

  render() {
    const classes = styles;
    return (
      <div style={loginStyle}>
        <Card style={classes.card}>
          <CardContent>
            <Card>
              <CardContent>
                <Typography variant="h4" component="h4" width="100%">
                  WELCOME {this.state.firstName}
                </Typography>
              </CardContent>
            </Card>
            <br />
            <Card>
              <CardContent>
                <Typography component="p">
                  FIRST NAME : {this.state.firstName}
                </Typography>

                <br />

                <Typography component="p">
                  MIDDLE NAME : {this.state.middleName}
                </Typography>

                <br />

                <Typography component="p">
                  LAST NAME : {this.state.lastName}
                </Typography>

                <br />

                <Typography component="p">
                  EMAIL : {this.state.email}
                </Typography>

                <br />

                <Typography component="p">
                  ALTERNATE EMAIL : {this.state.emailAlternate}
                </Typography>

                <br />

                <Typography component="p">
                  MOBILE : {this.state.mobileNumber}
                </Typography>

                <br />

                <Typography component="p">
                  EMERGENCY MOBILE : {this.state.emergencyContactNumber}
                </Typography>

                <br/>

                <Typography component="p">
                blood : {this.state.blood}
                </Typography>

                <br />

                <Typography component="p">D O B : {this.state.dob}</Typography>

                <br />

                <Typography component="p">
                  ADDRESS : {this.state.address}
                </Typography>
              </CardContent>
            </Card>
            <Button color="primary"
                  variant="outlined"
                  component={Link}
                  to="/profile-edit"
                  style={{marginTop:'2em'}}>
             <img src={edit} alt="edit icon" height="20" weight="20" /> &nbsp; Edit
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }
}
