import React from "react";
import {
  TextField,
  Button,
  Card,
  CardContent,
  Typography,
  Input,
  Grid,
  MenuItem,
  Select
} from "@material-ui/core/";
import firebase from "firebase";

var db = null;

const loginStyle = {
  position: "absolute",
  paddingTop: 10,
  left: "50%",
  transform: "translate(-50%)",
  width: "100%",
  maxWidth: "1100px"
};
const styles = {
  select: {
    marginTop: 32.15,
    width: 180
  },
  card: {
    width: "100%"
  },
  textField: {
    marginRight: 20
  },

  input: {
    display: "none"
  }
};

export default class ProfileEdit extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      flag: "",
      id: "",
      user: "",
      firstName: "",
      middleName: "",
      lastName: "",
      mobileNumber: "",
      emergencyContactNumber: "",
      email: "",
      emailAlternate: "",
      dob: "",
      address: "",
      state: "",
      city: "",
      country: "India",
      pincode: "",
      profilePicture: ""
    };

    db = firebase.firestore();
    db.settings({
      timestampsInSnapshots: true
    });

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  componentWillMount() {
    firebase.auth().onAuthStateChanged(user => {
      if(user) this.fetchProfileData(user);
    });
  }

  fetchProfileData(user){
    this.setState({ user });
    db.collection("users")
      .where("id", "==", this.state.user.uid)
      .get()
      .then(snap => {
        snap.docs.forEach(docs => {
          const data = docs.data();
          this.setState({
            middleName: data.middleName,
            firstName: data.firstName,
            lastName: data.lastName,
            email: data.email,
            emailAlternate: data.emailAlternate,
            mobileNumber: data.mobileNumber,
            blood: data.blood,
            dob: data.DOB,
            emergencyContactNumber: data.emergencyContactNumber,
            address: data.Address,
            state: data.state,
            city: data.city,
            pincode: data.pincode,
            country: data.country
          });
        });
      });
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  handleSubmit() {
    this.setUserInfoDB(this.state.user.uid);
  }

  setUserInfoDB(documentId){
    console.log(documentId)
    db.collection("users")
      .doc(documentId)
      .set({
        flag: true,
        id: this.state.user.uid,
        firstName: this.state.firstName,
        middleName: this.state.middleName,
        lastName: this.state.lastName,
        mobileNumber: this.state.mobileNumber,
        emergencyContactNumber: this.state.emergencyContactNumber,
        email: this.state.email,
        emailAlternate: this.state.emailAlternate,
        blood: this.state.blood,
        DOB: this.state.dob,
        Address: this.state.address,
        state: this.state.state,
        city: this.state.city,
        pincode: this.state.pincode,
        country: this.state.country,
        profilePicture: this.state.profilePicture
      }).then(_=>{
        window.location.href = 'profile';
      })
      .catch(function(err) {
        console.error(err);
      });
  }

  render() {
    const classes = styles;

    return (
      <div style={loginStyle}>
        <Card style={classes.card}>
          <CardContent>
            <form noValidate autoComplete="off">
              <Typography align="center" variant="h6" gutterBottom>
                Please edit your profile details
              </Typography>
              <TextField
                id="fname"
                label="First Name"
                required
                style={classes.textField}
                margin="normal"
                value={this.state.firstName}
                onChange={this.handleChange}
                name="firstName"
              />
              <TextField
                label="Middle Name"
                style={classes.textField}
                margin="normal"
                value={this.state.middleName}
                onChange={this.handleChange}
                name="middleName"
              />
              <TextField
                label="Last Name"
                required
                style={classes.textField}
                margin="normal"
                value={this.state.lastName}
                onChange={this.handleChange}
                name="lastName"
              />
              <br />
              <TextField
                label="Mobile Number"
                required
                type="tel"
                style={classes.textField}
                value={this.state.mobileNumber}
                error={this.state.mobileNumber.length !== 10}
                onChange={this.handleChange}
                name="mobileNumber"
                placeholder="+91 1234567890"
                margin="normal"
              />
              <TextField
                label="Emergency Phone Number"
                type="tel"
                value={this.state.emergencyContactNumber}
                onChange={this.handleChange}
                error={
                  this.state.mobileNumber === this.state.emergencyContactNumber
                }
                name="emergencyContactNumber"
                placeholder="+91 1234567890"
                style={{ width: 210 }}
                margin="normal"
              />
              <br />
              <TextField
                label="E-mail"
                type="email"
                required
                value={this.state.email}
                onChange={this.handleChange}
                name="email"
                style={classes.textField}
                placeholder="abcd@email.com"
                margin="normal"
              />
              <TextField
                label="Alternate E-mail"
                type="email"
                value={this.state.emailAlternate}
                error={this.state.email === this.state.emailAlternate}
                onChange={this.handleChange}
                name="emailAlternate"
                style={classes.textField}
                placeholder="abcd@email.com"
                margin="normal"
              />
              <br />

              <TextField
                label="blood"
                value={this.state.blood}
                onChange={this.handleChange}
                name="blood group"
                style={classes.textField}
                margin="normal"
              />

              <TextField
                type="date"
                name="dob"
                required
                value={this.state.dob}
                onChange={this.handleChange}
                style={classes.textField}
                margin="normal"
                helperText="date of birth "
              />

              <br/>

              <TextField
                style={{ width: 420 }}
                label="Address"
                type="text"
                required
                value={this.state.address}
                onChange={this.handleChange}
                name="address"
                margin="normal"
              />

              <br/>

              <TextField
                style={classes.textField}
                label="City"
                required
                value={this.state.city}
                onChange={this.handleChange}
                name="city"
                type="text"
                margin="normal"
              />

              <Select
                value={this.state.state}
                onChange={this.handleChange}
                displayEmpty
                style={classes.select}
                required
                name="state"
                label="state"
              >
                <MenuItem value={this.state.state?this.state.state:""}>{this.state.state?this.state.state:"Select State"}</MenuItem>
                {STATES_LIST.map(item=><MenuItem value={item}> {item} </MenuItem>)}
              </Select>

              <br/>

              <TextField
                style={classes.textField}
                label="Country"
                required
                type="text"
                value={this.state.country}
                onChange={this.handleChange}
                name="country"
                margin="normal"
              />
              <TextField
                style={classes.textField}
                label="Pin Code"
                required
                value={this.state.pincode}
                onChange={this.handleChange}
                name="pincode"
                type=""
                margin="normal"
              />
              <br />
              <Input
                accept="image/*"
                style={classes.input}
                id="outlined-button-file"
                multiple
                type="file"
              />
              {/* <label htmlFor="outlined-button-file">
                <Button
                  variant="outlined"
                  component="span"
                  className={classes.button}
                >
                  Choose file
                </Button>
                <Typography variant="overline">
                  *Upload Profile Picture
                </Typography>
              </label> */}
              <Grid container justify="flex-end">
                <Button
                  color="primary"
                  variant="outlined"
                  // type="submit"
                  // value="submit"
                  onClick={this.handleSubmit}
                >
                  Submit
                </Button>
              </Grid>
            </form>
          </CardContent>
        </Card>
      </div>
    );
  }
}

const STATES_LIST = [
    "Andhra Pradesh",
    "Arunachal Pradesh",
    "Assam",
    "Bihar",
    "Chhattisgarh",
    "Goa",
    "Gujarat",
    "Haryana",
    "Himachal Pradesh",
    "Jammu & Kashmir",
    "Jharkhand",
    "Karnataka",
    "Kerala",
    "Madhya Pradesh",
    "Maharashtra",
    "Manipur",
    "Meghalaya",
    "Mizoram",
    "Nagaland",
    "Odisha",
    "Punjab",
    "Rajasthan",
    "Sikkim",
    "Tamil Nadu",
    "Telangana",
    "Tripura",
    "Uttarakhand",
    "Uttar Pradesh",
    "West Bengal",

    "Andaman and Nicobar Islands",
    "Chandigarh",
    "Dadra and Nagar Haveli",
    "Daman & Diu",
    "Delhi",
    "Lakshadweep",
    "Puducherry",
];